@use '../util' as *;

html {
	font-size: 100%;
	box-sizing: border-box;
}

*,
*::before,
*::after { box-sizing: inherit }

body {
	margin: 0;
	font-size: rem(30);
	font-family: var(--font-body);
	color: var(--white);
	line-height: 1.3;
	overflow: hidden;
	cursor: url('../images/tools/normal.cur'), auto;
}

.wb-body {
	background-color: var(--dark-gray);
	color: var(--white);
	padding: 20px;
}

#click {
	width: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	z-index: 1;
	background-color: var(--black);

	&::after {
		content: '';
		width: rem(600);
		height: rem(335);
		background-image: url('../images/loading.webp');
	}
}

.section {

	&__title,
	&__subtitle { cursor: url('../images/tools/busy.cur'), auto }

	&__links {
		display: inline-flex;

		&:first-child {
			a {
				color: var(--cherry);
	
				&:not(:last-child) { padding-right: rem(20) }
			}
		}

		&:last-child {
			flex-direction: column;
		}
	}
}